<template>
  <div class="register">
    <h2>用户注册</h2>
    <!-- 注册信息框 -->
    <hr />
    <mt-field
      type="text"
      label="用户名"
      v-model="uname"
      placeholder="请输入用户名"
      :state="unstate"
      @blur.native.capture="checkUserName"
    ></mt-field>
    <hr />
    <mt-field
      type="password"
      label="密码"
      v-model="upwd"
      placeholder="请输入密码"
      :state="upstate"
      @blur.native.capture="checkUpwd"
    ></mt-field>
    <hr />
    <mt-field
      type="password"
      label="确认密码"
      v-model="confirmupwd"
      placeholder="请输入密码"
      :state="cupstate"
      @blur.native.capture="confirmUpwd"
    ></mt-field>
    <hr />
    <mt-button size="large" @click="register" type="primary" class="regBtn">注册</mt-button>
    <mt-button size="large" type="default" @click="back">返回</mt-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uname: "",
      upwd: "",
      confirmupwd: "",
      upstate: "",
      unstate: "",
      cupstate: ""
    };
  },
  methods: {
    // 检查用户名是否合法/存在
    checkUserName() {
      // console.log(111);
      // 1.创建正则表达式 字母数字3~12位
      var reg = /^[a-z0-9]{3,12}$/i;
      var dataObj = { uname: this.uname };

      // 2.验证用户名如果格式不正确，提示格式不正确
      if (!reg.test(this.uname)) {
        // this.$messagebox("消息","用户名格式不正确");
        this.unstate = "error";
        return;
      } else {
        this.unstate = "success";
        this.axios.get("/local/checkuname", { params: dataObj }).then(res => {
          // console.log(res)
          if (res.data.code == -1) {
            this.unstate = "error";
            this.$toast({
              message: res.data.msg,
              position: "middle",
              duration: "2000",
              iconClass: "iconfont icon-tishi"
            });
          } else if (res.data.code == 1) {
            this.unstate = "success";
          }
        });
        this.unstate = "success";
      }
    },
    checkUpwd() {
      // 1.创建正则表达式 字母数字3~12位
      var reg = /^[a-z0-9]{3,12}$/i;

      // 2.验证密码如果格式不正确，提示密码格式不正确
      if (!reg.test(this.upwd)) {
        // this.$messagebox("提示","密码格式不正确");
        this.upstate = "error";
        return;
      } else {
        this.upstate = "success";
      }
    },
    confirmUpwd() {
      var upwd = this.upwd;
      var cpwd = this.confirmupwd;

      if (cpwd === upwd) {
        this.cupstate = "success";
      } else {
        this.cupstate = "error";
        this.$toast({
          message: "两次输入密码不同",
          position: "middle",
          duration: "2000",
          iconClass: "iconfont icon-tishi"
        });
      }
    },
    register() {
      if (
        this.unstate == "success" &&
        this.upstate == "success" &&
        this.cupstate == "success"
      ) {
        let dataObj = { uname: this.uname, upwd: this.upwd };
        this.axios("/local/register", { params: dataObj }).then(res => {
          if (res.data.code == 1) {
            this.$toast({
              message: "注册成功",
              position: "middle",
              duration: "2000",
              iconClass: "iconfont icon-chenggong"
            });
            setTimeout(() => {
              this.$router.go(-1);
            }, 2000);
          } else {
            this.$toast({
              message: "注册失败",
              position: "middle",
              duration: "2000",
              iconClass: "iconfont icon-tishi"
            });
          }
        });
      } else {
        this.$toast({
          message: "信息填写有误",
          position: "middle",
          duration: "2000",
          iconClass: "iconfont icon-tishi"
        });
      }
    },
    back() {
        this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.register {
  margin-top: 20%;
}

.register h2 {
  text-align: center;
}

.regBtn {
    margin-bottom: 10px;
}
</style>